.tabs {
	&__control {
		display: flex;
		align-items: center;
		justify-content: center;
		&-item {
			&:not(:last-child) {
				margin-right: 30px; } } }
	&__wrapper {
		margin-top: 40px; }
	&__content {
		line-height: 2; }
	&__list {
		margin-top: 15px; }
	&__item {
		padding: 15px 0; } }


[data-tabs] {
	overflow: hidden;
	[data-tabs-control] {
		cursor: pointer;
		user-select: none;
		&[disabled],
		&.is-disabled {
			cursor: not-allowed;
			@include nosel; } }
	[data-tabs-wrapper] {
		position: relative; }
	[data-tabs-container] {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		opacity: 0;
		visibility: hidden;
		transition: opacity 0.3s, visibility 0.3s;
		&.is-open {
			position: relative;
			opacity: 1;
			visibility: visible; } } }
