//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }



$familyVerdana: 'Verdana';

@mixin verdana {
	font-family: 'Verdana', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif; }

$montserrat: 'Montserrat';

@mixin montserrat {
	font-family: 'Montserrat', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif; }


// Verdana

@font-face {
	@include fontdefault(normal, 400, $familyVerdana);
	@include fontpath('Verdana', 'Verdana', Verdana); }
@font-face {
	@include fontdefault(italic, 400, $familyVerdana);
	@include fontpath('Verdana-Italic', 'Verdana-Italic', Verdana-Italic); }
@font-face {
	@include fontdefault(normal, 700, $familyVerdana);
	@include fontpath('Verdana-Bold', 'Verdana-Bold', Verdana-Bold); }
@font-face {
	@include fontdefault(italic, 700, $familyVerdana);
	@include fontpath('Verdana-BoldItalic', 'Verdana-BoldItalic', Verdana-BoldItalic); }

// Montserrat

@font-face {
	@include fontdefault(normal, 400, $montserrat);
	@include fontpath('Montserrat', 'Montserrat', Montserrat); }
@font-face {
	@include fontdefault(italic, 400, $montserrat);
	@include fontpath('Montserrat-Italic', 'Montserrat-Italic', Montserrat-Italic); }
@font-face {
	@include fontdefault(normal, 700, $montserrat);
	@include fontpath('Montserrat-Bold', 'Montserrat-Bold', Montserrat-Bold); }
@font-face {
	@include fontdefault(italic, 700, $montserrat);
	@include fontpath('Montserrat-BoldItalic', 'Montserrat-BoldItalic', Montserrat-BoldItalic); }
