

// INSTRUCTOR
.instructor {
	height: 100%;
	background-color: $white;
	box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
	border-radius: 10px;
	overflow: hidden;
	transition: transform .15s;
	@include sm {
		border-radius: 7px; }
	@include hover {
		transform: translateY(-8px); }
	&__top {
		background-color: $blue;
		// padding-bottom: 34%
		height: 147px;
		position: relative;
		overflow: hidden;
		&:before {
			content: '';
			position: absolute;
			top: 1.33em;
			left: 2em;
			right: 2em;
			bottom: -1em;
			border: 1px solid $white;
			border-radius: 10px;
			@include md {
				top: 13px;
				left: 20px;
				right: 20px; } }
		@include ms {
			height: 98px; } }
	&__bottom {
		text-align: center;
		padding: 0 2em 2.66em;
		p {
			text-align: left;
			line-height: 1.9;
			@include sm {
				line-height: 1.75; } }
		@include md {
			padding: 0 20px 31px; } }
	&__img {
		@include circle(120);
		@include cover;
		// margin: -23% auto 0
		margin: -86px auto 0;
		overflow: hidden;
		z-index: 1;
		position: relative;
		@include ms {
			@include circle(81);
			margin-top: -58px; } }
	&__title {
		margin-top: 0.5em;
		@include md {
			&.title_md {
				font-size: 18px; } }
		@include ms {
			margin-top: 6px; } }
	&__aside {
		letter-spacing: 0.063em;
		color: $grayLight;
		margin-bottom: 1em;
		@include md {
			margin-bottom: 10px; } }
	&__btn {
		margin-top: 70px; } }

// TESTIMONIAL
.testimonial {
	height: 100%;
	background-color: $white;
	padding: 2em 2em 2.96em;
	box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
	border-radius: 10px;
	transition: transform .15s;
	@include hover {
		transform: translateY(-8px); }
	@include sm {
		padding: 20px 15px 30px; }
	&__top {
		display: flex;
		align-items: center; }
	&__img {
		flex-shrink: 0;
		@include circle(90);
		@include cover;
		overflow: hidden;
		margin-right: 20px;
		@include ms {
			@include circle(60);
			margin-right: 12px; } }
	&__title {
		@include ms {
			font-size: 14px;
			line-height: 1.5; } }
	&__aside {
		color: $grayLight;
		letter-spacing: 0.033em;
		@include ms {
			font-size: 11px; } }
	&__bottom {
		position: relative;
		margin-top: 1.6em;
		@include clr;
		@include sm {
			margin-top: 12px; }
		p {
			line-height: 1.9;
			@include sm {
				font-size: 14px;
				line-height: 1.65; } } }
	&__quote {
		width: 54px;
		height: 38px;
		margin-top: 7px;

		margin-right: -2px;
		float: left;
		background-image: url(../img/quotes.svg);
		@include cover;
		@include ms {
			width: 36px;
			height: 26px;
			margin-right: 2px; } } }

