// 										FORM
.form {
	// width: 100%
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	&__btn {
		@include lg {
			margin-top: 30px;
			text-align: center; }
		@include sm {
			margin-top: 14px; } }
	&__row {
		width: 100%;
		margin: 0 -10px;
		@include xs {
			margin: 0; } }
	&__col {
		padding: 0 10px;
		@include sm {
			&:not(:first-child) {
				margin-top: 14px; } }
		@include xs {
			padding: 0; } }
	.form-group {
		&.has-error {
			@include sm {
				margin-bottom: 16px; } } } }

.form-group {
	position: relative;
	input, textarea {
		color: $grayLight;
		@include verdana;
		@include f(15px, 400);
		letter-spacing: 0.066em;
		padding: 19px;
		background-color: $white;
		border-radius: 10px;
		width: 100%;
		border: 1px solid transparent;
		&.form-group__border-input {
			border: 1px solid rgba($black, 0.6);
			padding: 17px 19px;
			&.error {
				border: 1px solid $errorRed !important; } }
		&.error {
			border: 1px solid $orange !important; }
		&:focus {
			&::placeholder {
				font-size: 0; } }
		@include placeholder {
			color: $grayLight;
			@include verdana;
			@include f(15px, 400);
			letter-spacing: 0.066em; }
		@include sm {
			padding: 16px 20px; } }
	textarea {
		resize: none;
		padding: 41px 20px;
		display: block; } }

.checkbox {
	display: inline-block;
	position: relative;
	cursor: pointer;
	padding-left: 40px;
	user-select: none;
	line-height: 1.5;
	@include sm {
		font-size: 10px; }
	&__icon {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 0;
		width: 25px;
		height: 25px;
		border-radius: 4px;
		border: 1px solid $white; }
	a {
		display: inline;
		border-bottom: 1px solid transparent;
		transition: border-color .3s;
		@include hover {
			border-color: currentColor; } }
	input {
		position: absolute;
		width: 1px;
		height: 1px;
		margin: -1px;
		padding: 0;
		border: 0;
		clip: rect(0 0 0 0);
		overflow: hidden;
		&:checked {
			~ .checkbox__icon {
				background-image: url(../img/check.svg);
				background-size: 70%;
				background-repeat: no-repeat;
				background-position: 50% 50%; } }
		&.error {
			~ .checkbox__icon {
				border: 1px solid darken($orange, 20%); } } } }

.form-contacts {
	max-width: 880px;
	margin: 0 auto;
	&__row {
		margin: -10px; }
	&__col {
		padding: 10px;
		@include md {
			&:not(:first-child) {
				margin-top: 20px; } } }
	&__footer {
		margin-top: 20px;
		align-items: center;
		color: $white; }
	&__check {
		position: relative;
		@include md {
			margin-top: 20px; }
		.form-contacts & {
			.help-block.form-error {
				position: absolute;
				top: 100%;
				left: 30px; } } }
	.form-group.has-error {
		margin-bottom: 12px; } }

// VALIDATION ERROR STYLE
.help-block.form-error {
	padding: 2px 10px;
	// color: $errorRed
	// color: darken($orange, 20%)
	color: $white;
	@include f(10px, 500);
	line-height: 1.15;
 }	// text-transform: uppercase

// 										MAP
.map {
	width: 100%;
	height: 533px;
	@include xs {
		height: 300px; } }
// 										CONTACTS
.contacts {
	color: $black;
	position: relative;
	transition: transform .15s;
	@include hover {
		transform: translateY(-8px); }
	&__info {
		position: absolute;
		bottom: -127px;
		left: 50%;
		transform: translateX(-50%);
		z-index: 1;
		background-color: $white;
		@include md {
			position: static;
			transform: translateX(0);
			margin: 0 auto; } } }

.contacts-info {
	max-width: 350px;
	width: 100%;
	padding: 2em;
	border-radius: 10px;
	box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
	@include xs {
		padding: 30px 15px; }
	.footer & {
		padding: 0;
		border-radius: 0;
		box-shadow: none;
		@include md {
			margin-top: 40px; } }
	&__title {
		// margin-bottom: 12px
		&.title_md {
			@include md {
				font-size: 21px; } }
		.footer & {
			color: $white; } }
	&__aside {
		letter-spacing: 0;
		color: $grayLight;
		margin-top: 13px;
		@include sm {
			margin-top: 15px; }
		.footer & {
			color: $white;
			opacity: 0.5; } }
	// a
	// 	border-bottom: 1px solid transparent
	// 	transition: border-color .3s
	// 	+hover
	// 		border-color: currentColor
	address {
		font-style: normal;
		line-height: 1.73;
		letter-spacing: -0.003em;
		margin-top: 0; }
	&__phone {
		margin-top: 3px;
		line-height: 1.15;
		&:not(:last-child) {
			margin-bottom: 23px; } } }

// 										SMALL DOTS

.small__dot {
	@include circle(6);
	background-color: $grayLight;
	transition: background-color .3s, transform .3s;
	position: relative;
	@include ms {
		@include circle(4); }
	&:after {
		content: '';
		position: absolute;
		@include vcenter;
		width: 200%;
		height: 200%; }
	.slick-active & {
		background-color: $orange;
		transform: scale(2); } }
.small-dots {
	display: flex;
	width: 100%;
	justify-content: center;
	align-items: center;
	padding-top: 20px;
	li {
		line-height: 0;
		&:not(:first-child) {
			margin-left: 15px;
			@include ms {
				margin-left: 6px; } } } }


// BULLET
.bullet {
	display: flex;
	align-items: center;
	@include f(15px, 700);
	&__number {
		flex-shrink: 0;
		@include circle(45);
		border: 1px solid $grayLight;
		margin-right: 20px;
		position: relative;
		&:before {
			counter-increment: section;
			content: '0'counter(section);
			@include f(15px, 700);
			@include vcenter; } } }
// BULLET

// QUOTE
.quote-item {
	padding: 40px 30px;
	height: 100%;
	box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
	background-color: $white;
	border-radius: 10px;
	transition: transform .15s;
	@include sm {
		padding: 20px 15px; }
	@include hover {
		transform: translateY(-8px); }
	&__title {
		color: $black;
		@include f(21px, 700, 1.47);
		@include sm {
			font-size: 15px;
			line-height: 1.86; } } }
// QUOTE
