// GENERATE GRID EXAMPLE
// CUSTOM PADDING ON BREAKPOINTS - NOT INCLUDED IN MIXIN
@include generate_grid($columns: 6, $sizeXs: false, $sizeSm: false, $sizeMd: false, $sizeLg: true, $padding: 15px);

.inner {
	max-width: 1420px;
	margin: 0 auto;
	padding: 0 30px;
	@include md {
		padding: 0 16px; } }
.inner-md {
	max-width: 960px;
	margin: 0 auto;
	padding: 0 30px;
	@include md {
		padding: 0 16px; } }
.inner-sm {
	max-width: 825px;
	margin: 0 auto;
	padding: 0 30px;
	@include md {
		padding: 0 16px; } }


.section {
	&:not(:first-child) {
		padding-top: 150px;
		@include md {
			padding-top: 80px; } } }

.section-md {
	&:not(:first-child) {
		padding-top: 120px;
		@include md {
			padding-top: 80px; } } }

.section-sm {
	&:not(:first-child) {
		padding-top: 70px;
		@include sm {
			padding-top: 40px; } } }

.section,
.section-md,
.section-sm {
	&__title {
		&:not(:last-child) {
			margin-bottom: 70px;
			@include sm {
				margin-bottom: 37px; } } } }

.head {
	text-align: center;
	&:not(:last-child) {
		margin-bottom: 70px;
		@include md {
			margin-bottom: 36px; } } }

.bg-white {
	background-color: $white; }
